export default class cookieutils {
/**
* cookie
*/
  static setCookie (key, value) {
    var exDate = new Date()
    exDate.setTime(exDate.getTime() + 24 * 60 * 60 * 1000 * 15) // 15
    window.document.cookie = key + '=' + value + ';path=/;expires=' + exDate.toGMTString() // cookie
  }
  /**
 * cookie
 */
  static getCookie (param) {
    if (document.cookie.length > 0) {
      var arr = document.cookie.split('; ') // 
      for (var i = 0; i < arr.length; i++) {
        var arr2 = arr[i].split('=') // 
        if (arr2[0] === param) {
          this.c_param = arr2[1]
        }
      }
    }
    return this.c_param
  }
  /**
 * cookie
 */
  static clearCookie (key) {
    this.setCookie(key, '')
  }
  /**
 * cookie
 */
  static getCookieisEmpty (param) {
    if (this.getCookie(param) === undefined) {
      return true
    }
    return false
  }
}



